@import url(https://fonts.googleapis.com/css?family=Dancing+Script);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Player1,.Player2 {
  width: 50%;
  float: left;
  text-align: center;
}
.Player1{
  background-color: lightblue;
}
.Player2{
  background-color: palevioletred;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
 
  
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


/* Google font */
body {
  background-color: rgba(0,0,0,0.1);
}

/* styling paper */
.paper {
  width: 600px;
  min-height: 700px;
  position: relative;
  margin: 20px auto;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: white;
  box-shadow: 0px 0px 5px 0px #888;
}

/* styling red vertical line */
.paper::before {
  content: '';
  width: 3px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 60px;
  background-color:rgb(255 255 50 / 75%);
}

/* styling blue horizontal lines */
.pattern {
  height: 100%;
  background-image: repeating-linear-gradient(black 0px, black 24px, #3c3c3c 30px);

}
button,input, textarea{
	margin-top: 1rem;
	color: white;
	background: none;
	border: none;
	font-family: 'Dancing Script', cursive;
	font-size: 2rem;
}
button:hover{
	cursor: pointer;
}
textarea{
	font-size: 1.2rem;
	height: 60%;
}
input, textarea{
	width: 80%;
	border-width: 1px;
	border-color: grey;
	border-style: solid;
	border-top: 0;
}
/* styling text content */
.content {
  padding-top: 6px;
  padding-left: 76px;
  padding-right: 16px;
  line-height: 25px;
  font-family: 'Dancing Script', cursive;
  font-size: 19px;
  letter-spacing: 1px;
  word-spacing: 5px;
  color: white;
  text-overflow: ellipsis;
  word-wrap: break-word;
	overflow: hidden; 
}
.X{
	color: rgb(255 255 50 / 75%);
	font-size: 1.5rem;
	float: right;
}
.new, .back{
	
	float: right;
}
a{
	color: white;
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
	text-align: center;
}
a:hover{
	font-size: 1.6rem;
	color: rgb(255 255 50 / 75%);
}
